<template>
  <div class="elemental-reaction">
    <input id="-" type="radio" value="" name="-name" @change=" = ''" checked />
    <label for="-"> {{ displayName("") }} </label>
    <template v-if="damageResult.._">
      <input id="-_" type="radio" value="_" name="-name" @change=" = '_'" />
      <label for="-_" class="pyro">
        {{ displayName("") }}<span>{{
          Math.round(damageResult.._ * 100) / 100
        }}</span>
        <span class="savepoint" v-if="copiedDamageResult?.?._">
          <br />
          {{ displayName("") }}<span>{{
            Math.round(copiedDamageResult.._ * 100) / 100
          }}</span>
        </span>
      </label>
    </template>
    <template v-if="damageResult.._">
      <input id="-_" type="radio" value="_" name="-name" @change=" = '_'" />
      <label for="-_" class="hydro">
        {{ displayName("") }}<span>{{
          Math.round(damageResult.._ * 100) / 100
        }}</span>
        <span class="savepoint" v-if="copiedDamageResult?.?._">
          <br />
          {{ displayName("") }}<span>{{
            Math.round(copiedDamageResult.._ * 100) / 100
          }}</span>
        </span>
      </label>
    </template>
    <template v-if="damageResult.._">
      <input id="-_" type="radio" value="_" name="-name" @change=" = '_'" />
      <label for="-_" class="cryo">
        {{ displayName("") }}<span>{{
          Math.round(damageResult.._ * 100) / 100
        }}</span>
        <span class="savepoint" v-if="copiedDamageResult?.?._">
          <br />
          {{ displayName("") }}<span>{{
            Math.round(copiedDamageResult.._ * 100) / 100
          }}</span>
        </span>
      </label>
    </template>
    <template v-if="damageResult.._">
      <input id="-_" type="radio" value="_" name="-name" @change=" = '_'" />
      <label for="-_" class="pyro">
        {{ displayName("") }}<span>{{
          Math.round(damageResult.._ * 100) / 100
        }}</span>
        <span class="savepoint" v-if="copiedDamageResult?.?._">
          <br />
          {{ displayName("") }}<span>{{
            Math.round(copiedDamageResult.._ * 100) / 100
          }}</span>
        </span>
      </label>
    </template>
    <template v-if="damageResult..">
      <input id="-" type="radio" value="" name="-name" @change=" = ''" />
      <label for="-">
        {{ displayName("") }}
        <span>{{ Math.round(damageResult..) }}</span>
        <span class="savepoint" v-if="copiedDamageResult?.?.">
          <br />
          {{ displayName("") }}
          <span>{{ Math.round(copiedDamageResult..) }}</span>
        </span>
      </label>
    </template>
    <template v-if="damageResult..">
      <input id="-" type="radio" value="" name="-name" @change=" = ''" />
      <label for="-">
        {{ displayName("") }}
        <span>{{ Math.round(damageResult..) }}</span>
        <span class="savepoint" v-if="copiedDamageResult?.?.">
          <br />
          {{ displayName("") }}
          <span>{{ Math.round(copiedDamageResult..) }}</span>
        </span>
      </label>
    </template>
    <label v-if="damageResult.." class="pyro">
      {{ displayName("") }}
      <span>{{ Math.round(damageResult..) }}</span>
      <span class="savepoint" v-if="copiedDamageResult?.?.">
        <br />
        {{ displayName("") }}
        <span>{{ Math.round(copiedDamageResult..) }}</span>
      </span>
    </label>
    <label v-if="damageResult.." class="electro">
      {{ displayName("") }}
      <span>{{ Math.round(damageResult..) }}</span>
      <span class="savepoint" v-if="copiedDamageResult?.?.">
        <br />
        {{ displayName("") }}
        <span>{{ Math.round(copiedDamageResult..) }}</span>
      </span>
    </label>
    <label v-if="damageResult.." class="cryo">
      {{ displayName("") }}
      <span>{{ Math.round(damageResult..) }}</span>
      <span class="savepoint" v-if="copiedDamageResult?.?.">
        <br />
        {{ displayName("") }}
        <span>{{ Math.round(copiedDamageResult..) }}</span>
      </span>
    </label>
    <label v-if="damageResult.." :class="elementClass(damageResult..)">
      {{ displayName("") }}
      <span>{{ Math.round(swirlDmg) }}</span>
    </label>
    <label v-if="damageResult..">
      {{ displayName("") }}
      <span>{{ Math.round(damageResult..) }}</span>
      <span class="savepoint" v-if="copiedDamageResult?.?.">
        <br />
        {{ displayName("") }}
        <span>{{ Math.round(copiedDamageResult..) }}</span>
      </span>
    </label>
    <label v-if="damageResult.." class="pyro">
      {{ displayName("") }}
      <span>{{ Math.round(reactionDmg('')) }}</span>
      <span class="savepoint" v-if="copiedDamageResult?.?.">
        <br />
        {{ displayName("") }}
        <span>{{ Math.round(copiedDamageResult..) }}</span>
      </span>
    </label>
    <label v-if="damageResult.." class="dendro">
      {{ displayName("") }}
      <span>{{ Math.round(reactionDmg('')) }}</span>
      <span class="savepoint" v-if="copiedDamageResult?.?.">
        <br />
        {{ displayName("") }}
        <span>{{ Math.round(copiedDamageResult..) }}</span>
      </span>
    </label>
    <label v-if="damageResult.." class="dendro">
      {{ displayName("") }}
      <span>{{ Math.round(reactionDmg('')) }}</span>
      <span class="savepoint" v-if="copiedDamageResult?.?.">
        <br />
        {{ displayName("") }}
        <span>{{ Math.round(copiedDamageResult..) }}</span>
      </span>
    </label>
    <label v-if="damageResult.." class="dendro">
      {{ displayName("") }}
      <span>{{ Math.round(reactionDmg('')) }}</span>
      <span class="savepoint" v-if="copiedDamageResult?.?.">
        <br />
        {{ displayName("") }}
        <span>{{ Math.round(copiedDamageResult..) }}</span>
      </span>
    </label>
    <label v-if="damageResult..">
      {{ displayName("") }}
      <span>{{ Math.round(reactionDmg('')) }}</span>
      <span class="savepoint" v-if="copiedDamageResult?.?.">
        <br />
        {{ displayName("") }}
        <span>{{ Math.round(copiedDamageResult..) }}</span>
      </span>
    </label>
  </div>
  <fieldset>
    <template v-for="category in CATEGORY_LIST" :key="category">
      <template v-if="damageResult[category] && itemList(category).length > 0">
        <table v-if="resultStyleRef == '1'" class="result v-style">
          <thead>
            <tr @click="categoryOnClick(category)">
              <th>{{ displayName(category) }}</th>
              <th>{{ displayName("") }}</th>
              <th>{{ displayName("") }}</th>
              <th>{{ displayName("") }}</th>
            </tr>
          </thead>
          <template v-if="categoryOpenClose[category]">
            <tr v-for="(item, index) in itemList(category)" :key="index">
              <th class="with-tooltip" v-if="item[item.length - 1]" :rowspan="item[item.length - 1]">
                <span> {{ displayNameV(item[0]) }} </span>
                <span class="tooltip" v-html="displayDamageParam(item)" v-if="displayDamageParam(item)"></span>
              </th>
              <td :class="'damage-value ' + elementClass(item[1], item[5])">
                {{ displayDamageValue(item, 2, category) }}
                <span class="savepoint" v-if="displayCopiedDamageValue(index, 2, category)">
                  <br /> {{ displayCopiedDamageValue(index, 2, category) }}
                </span>
              </td>
              <td :class="'damage-value ' + elementClass(item[1], item[5])">
                {{ displayDamageValue(item, 3, category) }}
                <span class="savepoint" v-if="displayCopiedDamageValue(index, 3, category)">
                  <br /> {{ displayCopiedDamageValue(index, 3, category) }}
                </span>
              </td>
              <td :class="'damage-value ' + elementClass(item[1], item[5])">
                {{ displayDamageValue(item, 4, category) }}
                <span class="savepoint" v-if="displayCopiedDamageValue(index, 4, category)">
                  <br /> {{ displayCopiedDamageValue(index, 4, category) }}
                </span>
              </td>
            </tr>
          </template>
        </table>
        <table v-if="resultStyleRef == '0'" class="result h-style">
          <thead>
            <tr @click="categoryOnClick(category)">
              <th>{{ displayName(category) }}</th>
              <template v-for="item in itemList(category)" :key="item[0]">
                <th v-if="item[item.length - 1]" :colspan="item[item.length - 1]">
                  {{ displayNameH(item[0], category) }}
                </th>
              </template>
            </tr>
          </thead>
          <tr>
            <th>{{ displayName("") }}</th>
            <td v-for="item in itemList(category)" :key="item[0]"
              :class="'damage-value ' + elementClass(item[1], item[5])">
              {{ displayDamageValue(item, 2, category) }}
            </td>
          </tr>
          <template v-if="categoryOpenClose[category]">
            <tr>
              <th>{{ displayName("") }}</th>
              <td v-for="item in itemList(category)" :key="item[0]"
                :class="'damage-value ' + elementClass(item[1], item[5])">
                {{ displayDamageValue(item, 3, category) }}
              </td>
            </tr>
            <tr>
              <th>{{ displayName("") }}</th>
              <td v-for="item in itemList(category)" :key="item[0]"
                :class="'damage-value ' + elementClass(item[1], item[5])">
                {{ displayDamageValue(item, 4, category) }}
              </td>
            </tr>
          </template>
        </table>
      </template>
    </template>
    <hr />
    <table class="result damage-result">
      <tr>
        <th>{{ displayName("") }}</th>
        <td class="damage-value" v-for="item in damageTakenList" :key="item.key">
          <span :class="elementClass(item.key)">
            {{ Math.round(item.value) }}
          </span>
        </td>
      </tr>
      <tr>
        <th>{{ displayName("") }}</th>
        <td class="damage-value" v-for="item in resScoreList" :key="item.key">
          <span :class="elementClass(item.key)">
            {{ Math.round(item.value) }}
          </span>
        </td>
      </tr>
    </table>
  </fieldset>
  <div>
    <button type="button" @click="copyDamageResult">{{ displayName('') }}</button>
    <button type="button" @click="clearCopiedDamageResult">{{ displayName('') }}</button>
  </div>
  <div v-if="damageResult..length > 0">
    <ul class="notes">
      <li v-for="(item, index) in damageResult." :key="index">
        {{ item }}
      </li>
    </ul>
  </div>
  <div class="right">
    <label>
      <select v-model="resultStyleRef">
        <option value="1">V-style</option>
        <option value="0">H-style</option>
      </select>
    </label>
  </div>
</template>
<script lang="ts">
import _ from "lodash";
import i18n from "@/i18n";
import { overwriteObject } from "@/common";
import { TDamageResult, TDamageResultElementalReactionKey, TDamageResultEntry } from "@/input";
import { ELEMENT_COLOR_CLASS, TElementColorClassKey } from "@/master";
import { PropType, computed, defineComponent, reactive, ref, watch } from "vue";
import CompositionFunction from "./CompositionFunction.vue";
import { calculateReactedDamage } from "@/calculate";

export default defineComponent({
  name: 'DamageResult',
  props: {
    damageResult: { type: Object as PropType<TDamageResult>, required: true, },
  },
  setup(props) {
    const { displayName } = CompositionFunction();

    const CATEGORY_LIST = ['', '', '', '', '', ''];
    const  = ref('');
    const resultStyleRef = ref('1');
    const copiedDamageResult = reactive({} as TDamageResult);
    const categoryOpenClose = reactive({} as { [key: string]: boolean });

    watch(props.damageResult, (damageResult) => {
      if (
        (.value == '_' && damageResult.._ === 0) ||
        (.value == '_' && damageResult.._ === 0) ||
        (.value == '_' && damageResult.._ === 0) ||
        (.value == '_' && damageResult.._ === 0) ||
        (.value == '' && damageResult.. === 0) ||
        (.value == '' && damageResult.. === 0)
      ) {
        .value = '';
        (document.getElementById('-') as HTMLInputElement).checked = true;
      }
    })

    const swirlDmg = computed(() => {
      let result = props.damageResult..;
      if (.value == '_' && props.damageResult.. == '') {
        result *= props.damageResult.._;
      } else if (.value == '_' && props.damageResult.. == '') {
        result *= props.damageResult.._;
      } else if (.value == '_' && props.damageResult.. == '') {
        result *= props.damageResult.._;
      } else if (.value == '_' && props.damageResult.. == '') {
        result *= props.damageResult.._;
      } else if (.value == '' && props.damageResult.. == '') {
        result += props.damageResult..;
      }
      return result;
    })

    /**  */
    const damageTakenList = computed(() => {
      const result = props.damageResult..filter(
        (s: any) => s.key == ''
      ) as any;
      for (const entry of props.damageResult..filter(
        (s: any) => s.key != ''
      )) {
        const valueArr = result.map((s: any) => s.value);
        if (!valueArr.includes(entry.value)) {
          result.push(entry);
        }
      }
      return result;
    })

    /**  */
    const resScoreList = computed(() => {
      const result = props.damageResult..filter(
        (s: any) => s.key == ''
      ) as any;
      for (const entry of props.damageResult..filter(
        (s: any) => s.key != ''
      )) {
        const valueArr = result.map((s: any) => s.value);
        if (!valueArr.includes(entry.value)) {
          result.push(entry);
        }
      }
      return result;
    })

    const elementClass = (item: string, opt_kind?: string) => {
      let result = '';
      if (opt_kind === 'HP') {
        result = 'healing';
      } else if (opt_kind === 'HP') {
        result = 'hpcost';
      } else if (item) {
        result = ELEMENT_COLOR_CLASS[item as TElementColorClassKey];
      }
      return result;
    }

    const reactionDmg = (reaction: string): number => {
      let result = props.damageResult.[reaction as TDamageResultElementalReactionKey];
      if (result && _.isNumber(result)) {
        const critRate = props.damageResult.[(reaction + '') as TDamageResultElementalReactionKey];
        if (critRate && _.isNumber(critRate)) {
          const critDmg = props.damageResult.[(reaction + '') as TDamageResultElementalReactionKey];
          if (critDmg && _.isNumber(critDmg)) {
            result *= ((100 + critDmg) / 100 * critRate + (100 - critRate)) / 100;
          }
        }
      } else {
        result = 0;
      }
      return result;
    }

    function getAmplifyingReaction(dmgElement: string | null) {
      let reaction = ''; // 
      if (.value === '_' && dmgElement === '') {
        reaction = '';
      } else if (.value === '_' && dmgElement === '') {
        reaction = '';
      } else if (.value === '_' && dmgElement === '') {
        reaction = '';
      } else if (.value === '_' && dmgElement === '') {
        reaction = '';
      } else if (.value === '' && dmgElement === '') {
        reaction = '';
      } else if (.value === '' && dmgElement === '') {
        reaction = '';
      }
      return reaction;
    }

    const displayDamageValue = (dmgResultEntry: TDamageResultEntry, index: number, category: string) => {
      let value = Number(dmgResultEntry[index]);
      if (!value) return '-';
      if (!dmgResultEntry[5] || ![''].includes(dmgResultEntry[5])) {
        const dmgElement = dmgResultEntry[1]; // 
        const reaction = getAmplifyingReaction(dmgElement);
        const reactionTimes = category === '' && dmgResultEntry[0] === '' ? danCount.value : 1;
        value = calculateReactedDamage(dmgResultEntry, index, props.damageResult., reaction, reactionTimes);
      }
      if (value < 10) value = Math.round(value * 100) / 100;
      else if (value < 100) value = Math.round(value * 10) / 10;
      else value = Math.round(value);
      return value;
    }

    const displayCopiedDamageValue = (listIndex: any, index: number, category: string) => {
      let value = 0;
      if (category in copiedDamageResult) {
        const copied = copiedDamageResult.;

        let dmgResultEntry;
        const workResultEntryArr: TDamageResultEntry[] = (copiedDamageResult as any)[category];
        const curItem = itemList(category)[listIndex];
        if (listIndex < workResultEntryArr.length && curItem[0] == workResultEntryArr[listIndex][0]) {
          dmgResultEntry = workResultEntryArr[listIndex];
        } else {
          const workList = workResultEntryArr.filter((s: any[]) => s[0] == curItem[0]);
          if (workList.length == 0) return undefined;
          dmgResultEntry = workList[0];
          if (workList.length > 1) {
            for (let i = 0; i < itemList(category).length; i++) {
              if (itemList(category)[i][0] == curItem[0]) {
                if (i < listIndex) {
                  dmgResultEntry = workList[1];
                }
              }
            }
          }
        }

        if (dmgResultEntry === undefined) {
          return 0;
        }

        value = Number(dmgResultEntry[index]);
        if (!value) return '-';
        if (!dmgResultEntry[5] || ![''].includes(dmgResultEntry[5])) {
          const dmgElement = dmgResultEntry[1]; // 
          let reaction = getAmplifyingReaction(dmgElement);
          const reactionTimes = category === '' && dmgResultEntry[0] === '' ? danCount.value : 1;
          value = calculateReactedDamage(dmgResultEntry, index, copied, reaction, reactionTimes);
        }
        if (value < 10) value = Math.round(value * 100) / 100;
        else if (value < 100) value = Math.round(value * 10) / 10;
        else value = Math.round(value);
      }
      return value;
    }

    const itemList = (category: string) => {
      const result = [] as any[];
      const workList = (props.damageResult[category] as any[]).filter((s: any[]) => s[0] && !s[0].startsWith(''));
      for (let i = 0; i < workList.length; i++) {
        let span = 1;
        if (i > 0 && workList[i][0] == workList[i - 1][0]) span = 0;
        else {
          span = workList.slice(i).filter((s: any) => s[0] == workList[i][0]).length;
        }
        result.push([...workList[i], span]);
      }
      return result;
    }
    const danCount = computed(() => {
      return itemList('').filter(item => item[0].endsWith('') && !item[0].startsWith('_')).length;
    })

    for (const key of CATEGORY_LIST) {
      categoryOpenClose[key] = true;
    }

    const categoryOnClick = (category: string) => {
      categoryOpenClose[category] = !categoryOpenClose[category];
    }

    const displayNameV = (key: any): string => {
      if (key && i18n.global.locale.value == 'ja-jp' && key.length > 10) {
        let work = key;
        work = work.replace(/$/, '').replace(/$/, '');
        return work;
      }
      return displayName(key);
    }

    const displayNameH = (key: any, category: string): string => {
      if (key && i18n.global.locale.value == 'ja-jp' && itemList(category).length > 2) {
        let work = key;
        work = work.replace(/$/, '').replace(/$/, '');
        return work;
      }
      return displayName(key);
    }

    const displayDamageParam = (item: any): string => {
      let result = '';
      if (!item || !item[5] || ['HP', '', '', 'HP'].includes(item[5])) return '';
      result += displayName('');
      result += ':&nbsp;';
      result += Math.round((item[7] - 1) * 1000) / 10;
      result += '%<br>';
      if (item[2] && item[3] && item[4]) {
        let critRate = item[2] - item[4]; //  - 
        critRate /= item[3] - item[4]; //  - 
        critRate = Math.round(critRate * 1000) / 10;
        const critDmg = Math.round((item[3] / item[4] - 1) * 1000) / 10;
        result += displayName('');
        result += ':&nbsp;';
        result += critRate;
        result += '%/';
        result += critDmg;
        result += '%<br>';
      }
      if (item[6]) {
        result += item[6];
        result += 'Hit' + (item[6] > 1 ? 's' : '');
        result += '<br>';
      }
      return result;
    }

    const copyDamageResult = () => {
      overwriteObject(copiedDamageResult, _.cloneDeep(props.damageResult));
    }

    const clearCopiedDamageResult = () => {
      overwriteObject(copiedDamageResult, {});
    }

    return {
      displayName,
      displayNameV,
      displayNameH,

      ,
      swirlDmg,
      reactionDmg,
      elementClass,
      displayDamageValue,
      displayDamageParam,
      CATEGORY_LIST,
      itemList,
      categoryOpenClose,
      categoryOnClick,
      resultStyleRef,
      displayCopiedDamageValue,
      copiedDamageResult,

      damageTakenList,
      resScoreList,

      copyDamageResult,
      clearCopiedDamageResult,
    };
  },
});
</script>
<style scoped>
.elemental-reaction {
  margin-top: 5px;
  margin-bottom: 5px;
}

.elemental-reaction label {
  margin-top: 2px;
  margin-bottom: 2px;
}

.elemental-reaction [type="radio"]+label {
  background-color: black;
}

.elemental-reaction label span {
  white-space: nowrap;
}

table.result {
  width: 100%;
  margin: 1px auto;
  table-layout: fixed;
  border-spacing: 1px;
  border-collapse: collapse;
}

table.result tr {
  border-bottom: 1px solid gray;
}

table.result th,
table.result td {
  text-align: right;
  white-space: nowrap;
  padding-right: 4px;
  line-height: 3.3rem;
}

table.result th:first-child {
  width: 24rem;
  text-align: right;
  color: #df8f37;
  white-space: nowrap;
}

table.result thead th {
  color: #df8f37;
  background-color: #333;
}

table.result thead th:first-child {
  color: gold;
}

table.result.h-style th {
  white-space: normal;
  text-align: center;
}

table.result.h-style td {
  text-align: center;
}

table.result.h-style th:first-child {
  width: 13rem;
  text-align: right;
}

table.result.h-style thead th:first-child {
  text-align: left;
  padding-left: 1rem;
}

table.damage-result tr:last-child {
  border-bottom: none;
}

.right {
  text-align: right;
  margin: 1rem;
}

ul.notes {
  list-style-type: none;
  text-align: left;
  padding-inline-start: 0;
}

.tooltip {
  left: calc(100% - 10px);
  padding: 5px;
  border: 2px solid gray;
  border-radius: 10px;
  background-color: black;
}

span.savepoint {
  opacity: 60%;
}

button {
  margin-top: 5px;
  padding-left: 5px;
  padding-right: 5px;
}

.healing {
  color: #d0f8a8;
}

.hpcost {
  color: lightgray;
}
</style>
