<template>
  <template v-if="visible">
    <template v-if="mode == 1">
      <table class="character-info status">
        <caption>
          {{ displayName("") }}
        </caption>
        <template v-for="(item, index) in statsInfoList" :key="index">
          <tr>
            <th>{{ displayName(item.key) }}</th>
            <td>{{ displayStatValue(item.key, item.value) }}</td>
          </tr>
        </template>
      </table>
      <table class="character-info">
        <caption>
          {{ displayName("") }}
        </caption>
        <template v-for="(item, index) in constellationInfoList" :key="index">
          <tr>
            <td rowspan="2">
              <img class="icon" :src="item.icon_url" :alt="item." />
            </td>
            <th class="title">
              {{ displayName("" + (index + 1) + "") }} {{ displayName(item.) }}
            </th>
          </tr>
          <tr>
            <td class="description" v-html="item."></td>
          </tr>
        </template>
      </table>
      <table class="character-info">
        <caption>
          {{ displayName("") }}
        </caption>
        <template v-for="(item, index) in passiveTalentInfoList" :key="index">
          <tr>
            <td rowspan="2">
              <img class="icon" :src="item.icon_url" :alt="item." />
            </td>
            <th class="title">{{ displayName(item.) }}</th>
          </tr>
          <tr>
            <td class="description" v-html="item."></td>
          </tr>
        </template>
      </table>
    </template>
    <template v-if="mode == 2">
      <table class="character-info">
        <caption></caption>
        <tr>
          <th colspan="2" class="title">
            {{ displayName(characterMaster..) }}
          </th>
        </tr>
        <tr>
          <td colspan="2" class="description" v-html="characterMaster.."></td>
        </tr>
        <template v-for="(item, index) in talentNormalAttack('')." :key="index">
          <tr v-if="item. && !item..startsWith('_')">
            <th class="key">{{ displayName(item.) }}</th>
            <td class="value">
              {{ talentValue(item., levelNormalAttack("")) }}
            </td>
          </tr>
        </template>
        <template v-for="(item, index) in talentNormalAttack('')." :key="index">
          <tr v-if="item. && !item..startsWith('_')">
            <th class="key">{{ displayName(item.) }}</th>
            <td class="value">{{ talentValue(item., levelNormalAttack("")) }}</td>
          </tr>
        </template>
        <template v-for="(item, index) in talentNormalAttack('')." :key="index">
          <tr v-if="item. && !item..startsWith('_')">
            <th class="key">{{ displayName(item.) }}</th>
            <td class="value">
              {{ talentValue(item., levelNormalAttack("")) }}
            </td>
          </tr>
        </template>
      </table>
    </template>
    <template v-if="mode == 3">
      <table class="character-info">
        <caption></caption>
        <tr>
          <th colspan="2" class="title">
            {{ displayName(characterMaster..) }}
          </th>
        </tr>
        <tr v-for="(item, index) in talentAttributes(characterMaster., elementalSkillLevel)" :key="index">
          <th class="key">{{ displayName(item[0]) }}</th>
          <td class="value">{{ item[1] }}</td>
        </tr>
        <tr>
          <td colspan="2" class="description" v-html="characterMaster.."></td>
        </tr>
        <template v-for="(item, index) in characterMaster.." :key="index">
          <tr v-if="item. && !item..startsWith('_')">
            <th class="key">{{ displayName(item.) }}</th>
            <td class="value">{{ talentValue(item., elementalSkillLevel) }}</td>
          </tr>
        </template>
      </table>
    </template>
    <template v-if="mode == 4">
      <table class="character-info">
        <caption></caption>
        <tr>
          <th colspan="2" class="title">
            {{ displayName(characterMaster..) }}
          </th>
        </tr>
        <tr v-for="(item, index) in talentAttributes(characterMaster., elementalBurstLevel)" :key="index">
          <th class="key">{{ displayName(item[0]) }}</th>
          <td class="value">{{ item[1] }}</td>
        </tr>
        <tr>
          <td colspan="2" class="description" v-html="characterMaster.."></td>
        </tr>
        <template v-for="(item, index) in characterMaster.." :key="index">
          <tr v-if="item. && !item..startsWith('_')">
            <th class="key">{{ displayName(item.) }}</th>
            <td class="value">{{ talentValue(item., elementalBurstLevel) }}</td>
          </tr>
        </template>
      </table>
    </template>
  </template>
</template>

<script lang="ts">
import _ from "lodash";
import { getStatValueByLevel } from "@/calculate";
import { TCharacterDetail } from "@/master";
import { computed, defineComponent, PropType } from "vue";
import CompositionFunction from "./CompositionFunction.vue";

type TConstellationInfo = {
  : string;
  : string;
  icon_url: string;
  [key: string]: any;
};

type TTalentInfo = {
  : string;
  : string;
  icon_url: string;
  [key: string]: any;
};

export default defineComponent({
  name: 'CharacterInfo',
  props: {
    visible: { type: Boolean, required: true },
    mode: { type: Number, required: true },
    characterMaster: { type: Object as PropType<TCharacterDetail>, required: true },
    ascension: { type: Number, required: true },
    level: { type: Number, required: true },
    normalAttackLevel: { type: Number, required: true },
    elementalSkillLevel: { type: Number, required: true },
    elementalBurstLevel: { type: Number, required: true },
    normalAttackReplacing: { type: Array as PropType<boolean[]>, required: true },
  },
  setup(props) {
    const { displayName, displayStatValue } = CompositionFunction();

    const statsInfoList = computed((): any[] => {
      const result = [] as any[];
      if ('' in props.characterMaster) {
        for (const key of Object.keys((props.characterMaster as any).)) {
          const valueObj = (props.characterMaster as any).[key];
          const value = getStatValueByLevel(valueObj, props.ascension, props.level);
          result.push({ key: key, value: value });
        }
      }
      return result;
    });

    const constellationInfoList = computed((): TConstellationInfo[] => {
      const result = [] as TConstellationInfo[];
      if ('' in props.characterMaster) {
        for (const key of Object.keys((props.characterMaster as any).)) {
          result.push((props.characterMaster as any).[key]);
        }
      }
      return result;
    });

    const passiveTalentInfoList = computed((): TTalentInfo[] => {
      const result = [] as TTalentInfo[];
      if ('' in props.characterMaster) {
        for (const entry of (props.characterMaster as any).) {
          result.push(entry);
        }
      }
      return result;
    });

    const talentNormalAttack = (key: string) => {
      let talentObj;
      if (key == '') {
        talentObj = props.normalAttackReplacing[0]
          ? props.characterMaster.
          : props.characterMaster.;
      } else if (key == '') {
        talentObj = props.normalAttackReplacing[1]
          ? props.characterMaster.
          : props.characterMaster.;
      } else if (key == '') {
        talentObj = props.normalAttackReplacing[2]
          ? props.characterMaster.
          : props.characterMaster.;
      }
      return talentObj;
    };

    const levelNormalAttack = (key: string) => {
      let level = props.normalAttackLevel;
      const talentObj = talentNormalAttack(key);
      if ('' in talentObj) {
        switch (talentObj['']) {
          case '':
            level = props.elementalSkillLevel;
            break;
          case '':
            level = props.elementalBurstLevel;
            break;
        }
      }
      return level;
    };

    const talentAttributes = (talentObj: TTalentInfo, level: number) => {
      const result = [] as any[];
      ['', '', ''].forEach((suffix) => {
        const keyArr = Object.keys(talentObj).filter((s) => s.endsWith(suffix));
        for (const key of keyArr) {
          let value = talentObj[key];
          if (_.isPlainObject(value)) {
            value = value[String(level)];
          }
          result.push([key, value]);
        }
      });
      return result;
    };

    const talentValue = (valueObj: any, level: number) => {
      if (_.isPlainObject(valueObj)) {
        return valueObj[String(level)];
      }
      return valueObj;
    };

    return {
      displayName,
      displayStatValue,

      statsInfoList,
      constellationInfoList,
      passiveTalentInfoList,

      talentNormalAttack,
      levelNormalAttack,
      talentAttributes,

      talentValue,
    };
  },
});
</script>

<style scoped>
table {
  width: calc(100% - 1rem);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  border: 2px solid gray;
  border-spacing: 0;
}

th,
td {
  padding: 2px 8px;
  border: 1px solid gray;
  border-spacing: 0;
  text-align: left;
}

th.title {
  color: burlywood;
  text-align: center;
}

th.key {
  color: burlywood;
}

td.value {
  text-align: right;
}

img.icon {
  width: 60px;
  height: 60px;
}
</style>
